@import "sass/index";

@keyframes _1_animation {
  0% {
    transform: translate(0%, 0%);
  }

  100% {
    transform: translate(-10%, 5%);
  }
}

@keyframes _2_animation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(20deg);
  }
}

@keyframes _3_animation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-130deg);
  }
}

.coin {
  position: absolute;

  &__1 {
    width: 17% !important;
    height: auto !important;

    right: -2%;
    bottom: -30%;
    animation: _1_animation 4s steps(50, end) infinite alternate;
  }
  &__2 {
    width: 4% !important;
    height: auto !important;

    top: 45%;
    right: 13%;
    animation: _2_animation 5s steps(25, end) infinite alternate;
  }
  &__3 {
    width: 9% !important;
    height: auto !important;

    left: auto;
    right: 34%;
    top: 40%;
    animation: _3_animation 9s steps(150, end) infinite alternate;
  }
}
