@import "sass/index";

@keyframes coinAnimation {
  0% {
    transform: translateY(0%);
  }

  100% {
    transform: translateY(20%);
  }
}

.coin {
  position: absolute;
  animation: coinAnimation 4s steps(60, end) infinite alternate;

  &__1 {
    width: 10% !important;
    height: auto !important;

    bottom: 0%;
    right: 33%;
  }
  &__2 {
    width: 7% !important;
    height: auto !important;

    right: 24%;
    top: 51%;
  }
  &__3 {
    width: 8% !important;
    height: auto !important;

    right: 14%;
    top: -13%;
  }
  &__4 {
    width: 3% !important;
    height: auto !important;

    right: 12%;
    top: 23%;
  }
  &__5 {
    width: 4.5% !important;
    height: auto !important;

    top: -4%;
    right: 3%;
  }
}
