@import "sass/index";

@keyframes buttonAnimate {
  0% {
    transform: scale(0.95);
  }

  100% {
    transform: scale(1.05);
  }
}

@keyframes buttonShadow {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.container {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  width: 100%;
  height: 100%;

  background: radial-gradient(
    156.66% 156.66% at -19.19% 139.37%,
    #ffab67 0%,
    #fc946b 2.19%,
    #f1477a 10.05%,
    #ed2980 13.67%,
    #df287e 23.05%,
    #ba257a 40.76%,
    #7f2174 64.96%,
    #2d1a6b 94.42%,
    #1d1969 100%
  );
  border-radius: 20px;

  &:hover,
  &:focus,
  &:active {
    & .img {
      transform: scale(1.03);
    }
    & .text {
      transform: scale(1.05);
    }
  }
}

.img {
  transition: all 0.4s;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: auto !important;
  padding: 5% 0 3.5% 4%;
}

.text {
  text-transform: uppercase;
  font-family: "Gilroy";
  letter-spacing: 0.03em;
  font-size: 1.78vw;
  font-weight: 900;
  color: #ffffff;
  width: fit-content;
  position: absolute;
  top: 48%;
  left: 4%;
  margin: 0;

  & span {
    color: #fed61c;
    position: relative;

    &::after {
      content: "â€¯1,000X";
      filter: drop-shadow(0px 0px 5px #fed61c);
      line-height: 1.3;
      animation: buttonShadow 1.5s steps(10, end) infinite alternate;

      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.img,
.text {
  transition: all 0.4s;
}

.buttonWrapper {
  position: absolute;
  left: 3.5%;
  bottom: 13%;
  width: 30%;
  height: 20%;

  animation: buttonAnimate 1.5s steps(30, end) infinite alternate;

  &::before {
    content: "";

    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    border-radius: 10vw;
    box-shadow: 0px 0px 30px 10px rgba(52, 231, 255, 0.8);
    animation: buttonShadow 1.5s steps(13, end) infinite alternate;
  }
}

.button {
  background: #34e7ff !important;
  border-radius: 10vw;

  font-family: "Gilroy";
  font-weight: 700;
  font-size: 2.9vw;
  line-height: 1;
  text-transform: uppercase;

  color: #000000;
  white-space: nowrap;
  width: 100%;
  height: 100%;
  padding: 0;
  display: block;
}

.disable {
  & * {
    animation: none !important;
    filter: none !important;
  }
}
