@import "sass/index";

@keyframes coinAnimation {
  0% {
    transform: translateY(0%);
  }

  100% {
    transform: translateY(-15%);
  }
}

.coin {
  position: absolute;
  animation: coinAnimation 3s steps(60, end) infinite alternate;

  &__1 {
    width: 5% !important;
    height: auto !important;

    right: 6%;
    bottom: 17%;
  }
  &__2 {
    width: 5.5% !important;
    height: auto !important;

    right: 21%;
    top: 27%;
  }
  &__3 {
    width: 12% !important;
    height: auto !important;

    right: 18%;
    bottom: -18%;
  }
  &__4 {
    width: 18% !important;
    height: auto !important;

    left: 36%;
    top: 65%;
  }
}
